.ul {
  display: grid;
  max-width: 85vw;
  grid-template-columns: repeat(2, 40vw);
  gap: 5vw;
  margin: 7.5vw 0;

  @media (min-width: 991px) {
    grid-template-columns: repeat(4, 20vw);
    gap: 1.66vw;
    margin: 50px 0;
  }

  @media (min-width: 768px) {}

}

.buttonsWrapper {
  display: flex;
  width: 100%;
  height: 40px;
  justify-content: space-between;
}

.searchWrapper {
  @media (max-width: 990px) {
    display: none;
  }

  display: flex;
  align-items: center;
  position: relative;

  img {
    position: absolute;
    left: 10px;
    width: 20px;
  }
}

.search {
  width: 20vw;
  max-width: 400px;
  padding: 0 20px 0 35px;
  height: 100%;
}